import { Checklist,Toast } from 'mint-ui';
import rHeader from "@/components/header/header-r"

export default{
    name:"load",
    data(){
        return {
            click_state:true,
            is_setBtn:false,
            getBtn:"获取",
            time:60,
            yzm:"666666",
            tel:"",
            yqm:"",
            confirm_psw:"",
            clause:"1"
        }
    },
    methods:{
        //注册提交
        reg_fn(){
            if(!this.yzm){
                Toast("请输入验证码");
                return false;
            }else if(!this.tel){
                Toast("请输入新的手机号");
                return false;
            }

            this.$store.dispatch("forgetPassword", {
                data: "data="+JSON.stringify({
                    mobile:this.tel,
                    editcode:this.yzm,
                    newpassword:this.psw
                }),
                fn: (code, msg) => {
                    switch (code) {
                        case 200:
                            let instance = Toast('修改成功');
                            setTimeout(() => {
                                instance.close();
                                this.$router.go(-1);
                            }, 2000);
                            break;
                        case -1:
                            Toast(msg)
                            break;
                        default:
                            Toast("请求出错")
                            break;
                    }
                }
            })
        },
        //获取验证码
        getCode(){
            if(this.tel==""){
                Toast("请输入手机号码")
                return false;
            }else if(!(/^1[3578]\d{9}$/.test(this.tel))){
                Toast("您输入的手机号码有误");
                return false;
            }

            if(this.click_state){
                this.click_state = false;
                this.$store.dispatch("verificationCode",{
                    data:`data=${JSON.stringify({mobile:this.tel,type:'MODIFYPWD'})}`,
                    fn:(code,msg)=>{
                        switch (code) {
                            case 200:
                                //const date = new Date();
                                this.countDown(msg.limit_time);
                                Toast("验证码获取成功");
                                break;
                            case -1:
                                Toast(msg);
                                this.click_state = true;
                                break;
                            default:
                                Toast("请求出错");
                                this.click_state = true;
                                break;
                        }
                    }
                });
            }
        },
        zeroFill(time){
            return time<10?`0${time}`:time;
        },
        countDown(time){
            setTimeout(()=>{
                --time;
                if(time<1){
                    time = 60;
                    this.getBtn = "重新获取";
                    this.is_setBtn = false;
                    this.click_state = true;
                }else{
                    this.is_setBtn = true;
                    this.getBtn = `${this.zeroFill(time)}S 后可重新获取`;
                    this.countDown(time);
                }
            },1000);
        },
        target_handle(e){

            e.stopPropagation();
            e.preventDefault();
            return false;
        }
    },
    components:{
        Toast,Checklist,rHeader
    }
}



